<template>
  <div class="tags">
    <div class="main">
      <div class="d-title">标签</div>
      <div class="content display-flex">
        <div class="inner" v-for="(item,index) of tags" :key="index" @click="gotoDetails(item)">{{item}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Tags',
    data() {
      return {
        url: '/article',
        tags: []
      }
    },
    created() {
      this.getTagsCount();
    },
    methods: {
      // 查询标签总数
      getTagsCount: function() {
        this.$axios.get(this.url + '/queryTags').then(res => {
          if (res.data.code == 200) {
            let tags = [];
            for (let i = 0; i < res.data.body.length; i++) {
              tags = tags.concat(res.data.body[i].article_tags.split(','));
            }
            this.tags = [...new Set(tags)];
          } else {
            this.$message({
              message: res.data.message,
              type: 'warning'
            })
          }
        })
      },
      // 查询
      gotoDetails: function(tag) {
        this.$router.push({
          name: 'Article',
          params: {
            article_title: tag
          }
        })

        this.getArticle(tag);
      },

      getArticle: function(tag) {
        if (tag != '') {
          this.$axios.get(this.url + '/queryArticleByTitle/' + tag).then(res => {
            if (res.data.code == 200) {
              this.$store.state.article_group = res.data.body;
            } else {
              this.$store.state.article_group = [];

              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }
          })
        }
      }
    }
  }
</script>

<style scoped>
  .content {
    flex-wrap: wrap;
  }

  .inner {
    padding: 5px 10px;
    border-radius: 5px;
    margin: 5px;
    cursor: pointer;
  }

  .inner:nth-child(4n-1) {
    background-color: #67C23A;
    color: #fff;
  }

  .inner:nth-child(4n-2) {
    background-color: #E6A23C;
    color: #fff;
  }

  .inner:nth-child(4n-3) {
    background-color: #F56C6C;
    color: #fff;
  }

  .inner:nth-child(4n) {
    background-color: #409EFF;
    color: #fff;
  }
</style>



